<template>
    <div>
      <!-- 头部 -->
<header>
       <i class="iconfont icon-arrow-right" @click="prev"> </i>
        <span>个人信息</span>
       <i class="iconfont icon-bianji"  @click="update"> </i>
    </header>
       <!-- 主体内容 -->
      <div class="avator">
          <span>头像</span>
          <div class="avatar-uploader">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
         </div>
      </div>
      <div class="indentity">
        <span>姓名</span>
        <em>{{objs.username}}</em>
    </div>
    <div class="indentity">
        <span>身份证</span>
           <em>{{objs.id_card}}</em>
    </div>
    <div class="indentity">
        <span>家庭住址</span>
          <em>{{objs.hometown}}</em>
    </div>
     <div class="indentity">
        <span>工作地址</span>
          <em>{{objs.address}}</em>
    </div>
     <div class="indentity">
        <span>民族</span>
         <em>{{objs.nation}}</em>
    </div>
     <div class="indentity">
        <span>微信号</span>
         <em>{{objs.wx_num}}</em>
    </div>
     <div class="indentity">
        <span> qq号</span>
         <em>{{objs.qq_num}}</em>
    </div>
     <div class="indentity">
        <span>性别</span>
         <em>{{objs.sex?"男":"女"}}</em>
    </div>
     <div class="indentity">
        <span>最高学历</span>
           <em>{{objs.education}}</em>
    </div>
     <div class="indentity">
        <span>职称</span>
           <em>{{objs.job_rank}}</em>
    </div>
      <div class="indentity">
        <span>薪资水平</span>
           <em>{{objs.salary}}</em>
    </div>
      <div class="indentity">
        <span>入党时间</span>
           <em>{{objs.join_party_time}}</em>
    </div>
      <div class="indentity">
        <span>党费最后缴纳时间</span>
           <em>{{objs.last_pay_time}}</em>
    </div>
    <div class="indentity">
        <span>当前身份</span>
           <em>{{objs.party_status ?"党员" : objs.party_status==1?"预备党员":'积极分子'}}</em>
    </div>
 

    </div>
</template>

<script>
import { getUserInfo } from "../../api/api"
import { showLoading, hideLoading } from '../../components/loading';
export default {
 name:'information',
     data(){
      return{
          imageUrl: '',
         objs:{} //信息集合
      }
    },
    created(){
      this.getInfo();
    },
    methods:{
      async getInfo(){
         showLoading();
         if(localStorage.getItem("userId")==''){
           this.$message({
             message:'请先登录'
           })
              hideLoading();
           return
         }
         let result= await getUserInfo({'userID':localStorage.getItem("userId")})

        if(result.status==0){
           this.objs=result.data;
           this.imageUrl='http://118.178.85.48.:4000'+result.data.header
             localStorage.setItem("tx",this.imageUrl)
             localStorage.setItem("username",result.data.username)
             localStorage.setItem("total_score",result.data.total_score)
             hideLoading();
         }
         hideLoading();
        
      },
        update(){
           this.$router.push({name:'updateInfo',params:{objs:this.objs}})
        },
     
   prev(){
          this.$router.go(-1)
      },
    // 上传成功回调
  }
}
   window.onload = function () {
            let fs = document.documentElement.clientWidth * 100 / 720;
            document.querySelector("html").style.fontSize = fs + "px";
        }
</script>

<style lang="less" scoped>
 @import "../../assets/less/04-个人信息.less";
  @import "../../assets/less/zcm.less";

 
  header{
    background-color: #c50206;
    .lh(100);
    .fs(33);
    color: #fff;
    .flex_a();
 .icon-arrow-right{
     .fs(50);
     .ml(-80);
 }
    .icon-bianji{
        .fs(40);
    }
}
em{
  .mr(50);
  color:#999999; 
  .fs(25);
}

 .avatar-uploader{
  .w(150);
  .h(100);
  .mr(30);
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  background: url('../../assets/Img/1.jpg') no-repeat;
  background-size: 100% 100%;
  img{
  .w(150);
  .h(100);
  }
}

</style>