<template>
  <div class="">
    <HeaderBar :Headname="Pagetitle"></HeaderBar>
    <div 
        class="img-list"
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="0"
        infinite-scroll-immediate-check="false">
        <router-link 
        :to="{path:'/newsDetail',query: {NewsId: item.news_id}}" 
        v-for="item in NewsList" 
        :key="item.news_id"
        class="item">
                <div class="bg-img">
                <img :src="'http://118.178.85.48:4000'+item.pic"/>
                </div>
                <div class="overflow-line-2">
                {{item.title}}
                </div>
        </router-link>
    
    </div>
  </div>
</template>

<script>
import HeaderBar from "../../../components/HeaderBar";
import { Loading } from 'element-ui';
import { getNewsDetailById, getNewsList } from '../../../api/api';
import { formateDate } from '../../../api/util';

export default {
  name: "",
  components: {
    HeaderBar,
  },
  data() {
    return {
        Pagetitle: "随时随地拍",
        NewsList:[],
        page:1,
        limit:9,
        total:'',
        msg:'加载更多中...',
        loading:false,
    };
  },
  created(){
      this.getNewsList();
  },
  methods:{
    // 获取新闻列表
    async  getNewsList(){
        let res = await getNewsList({page:this.page,limit:this.limit,type:7});
        console.log(res);
        this.NewsList = [ ...this.NewsList, ...res.data]
        this.loading = false;
        this.total = res.total
        if(this.NewsList.length == this.total){
          this.msg = '------没有数据啦------'
          return;
        }
    },
    //加载更多
    loadMore() {
      this.loading = true //不触发loadMore函数
      this.page++;
        // console.log(this.page);
      if(this.NewsList.length == this.total){
          this.msg = '------没有数据啦------'
          return;
      }
      this.getNewsList();
    },
  }
};

</script>
<style lang='less' scoped>
.img-list{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    a{
        width: 3rem;
        height: 3rem;
        padding: 0.3rem;
        padding-bottom: 0;
        .bg-img{
            width: 100%;
            height: 70%;
            img{
                height: 1.8rem;
                width: 3rem;
            }
        }
        .overflow-line-2{
            // padding: 0 0.3rem;
            font-size: 0.3rem;
            line-height: 1.5;
            overflow: hidden;
            text-overflow: ellipsis; //超出被截断
            display: -webkit-box;  
            -webkit-line-clamp: 1;  //显示两行
            -webkit-box-orient: vertical; //显示省略号
            color: #666;
        }
    }
}
</style>