<template>
  <div class="">
        <HeaderBar :Headname='Pagetitle'></HeaderBar>
        <img :src="'http://118.178.85.48:4000'+imgurl" alt="">
        <div class="content" v-html='content'></div>
  </div>
</template>

<script>
import { getNewsDetailById } from '../../api/api';
import HeaderBar from "../../components/HeaderBar";

export default {
  name: "",
  components: {
      HeaderBar
  },
  data() {
    return {
        Pagetitle:'',
        content:'',
        id:'',
        imgurl:'',
    }
  },
  created(){
      this.id = this.$route.query.NewsId;
    //   console.log(this.id);
      this.getNewsById();
  },
  methods:{
      async getNewsById(){
          let res = await getNewsDetailById(this.id);
          console.log(res);
          this.Pagetitle = res.data.typeName
          this.content = res.data.content
          this.imgurl = res.data.pic
      }
  },
};
</script>
<style lang='less' scoped>
img{
    width: 100%;
}
body{
   box-sizing:border-box;
}

.content{
    padding: 0.2rem;
    box-sizing:border-box
}
h3{

}
p{
    margin: 0 0 0.2rem;
    font-size: 0.26rem;
}
span{
    font-size: 0.26rem;
    line-height: 2;
}
</style>