<template>
    <div class='score'>
      <!-- 头部 -->
  <HeaderBar :Headname='Pagetitle'></HeaderBar>
       <!-- 主体部分 -->
 <section>
        <div class="pwd">
            <p>旧密码：</p>   
                <input type="password" placeholder="输入旧密码" v-model="oldPassword">
        </div>
        <div class="pwd">
            <p>新密码：</p>
            <input type="password" placeholder="输入新密码" v-model="newPassword">
         
        </div>
          <div class="pwd">
            <p>确认密码：</p>   
              <input class="las" type="password" placeholder="再次输入新密码" v-model="rePassword">
        </div>
        <div>
            <button @click="sub">提交</button>
        </div>
    </section>
    </div>
</template>

<script>
import HeaderBar from "../../components/HeaderBar";
import {updatePwd} from '../../api/api'
import { showLoading, hideLoading } from '../../components/loading';
export default {
 name:'information',
     data(){
      return{
            oldPassword:'',
            newPassword:'',
            rePassword:'',
         Pagetitle:'修改密码'
      }
    },
     components:{
      HeaderBar
    },
    methods:{
        sub(){
            if(this.oldPassword==''||this.newPassword==''||this.rePassword==''){
                this.$message({
                    message:"请填写完整",
                    type:'error'
                })
                    return
            }
            if(this.rePassword!=this.newPassword){
                  this.$message({
                    message:"确认密码不一致",
                    type:'error'
                })
                return
            }
            let obj={'id_card':localStorage.getItem("id"),'oldPassword':this.oldPassword,'newPassword':this.newPassword};
            showLoading();
            updatePwd(obj).then(data=>{
                console.log(data);
                if(data.status==0){
                        this.$message({
                    message:data.massage,
                    type:'success'
                })
                this.oldPassword='';
                this.newPassword='';
                this.rePassword='';
                hideLoading();
                }else{
                     this.$message({
                    message:data.massage,
                    type:'error'
                })
         hideLoading();   
                }
            }).catch(()=>{
                this.$message({
                    message:"网络延迟",
                    type:'error'
                })

            })
            
        }
    }
}
   window.onload = function () {
            let fs = document.documentElement.clientWidth * 100 / 720;
            document.querySelector("html").style.fontSize = fs + "px";
        }
</script>

<style lang="less" scoped >
 @import "../../assets/less/修改密码.less";
@import "../../assets/css/normalize.css";
</style>