<template>
    <div>
      <!-- 头部 -->
<header>
       <i class="iconfont icon-arrow-right" @click="prev"> </i>
        <span>修改个人信息</span>
       <i class="iconfont icon-save"  @click="save"> </i>
    </header>
       <!-- 主体内容 -->
      <div class="avator">
          <span>头像</span>
            <el-upload
                class="avatar-uploader"
                action=''
                :auto-upload='false'
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :on-change='change'
                :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <span v-if="imageUrl" class="el-upload-action">
                    <i class="el-icon-delete"></i>
                </span>
                <i v-else class="el-icon-upload2 avatar-uploader-icon" stop></i>
            </el-upload>
      </div>
      <div class="indentity">
        <span>姓名</span>
        <input type="text"  v-model="objs.username"/>
    </div>
    <div class="indentity">
        <span>身份证</span>
      <input type="text"  v-model="objs.id_card"/>
    </div>
    <div class="indentity">
        <span>家庭住址</span>
         <input type="text"  v-model="objs.hometown"/>
    </div>
     <div class="indentity">
        <span>工作地址</span>
         <input type="text"  v-model="objs.address"/>
    </div>
     <div class="indentity">
        <span>民族</span>
         <input type="text"  v-model="objs.nation"/>
    </div>
     <div class="indentity">
        <span>微信号</span>
         <input type="text"  v-model="objs.wx_num"/>
    </div>
     <div class="indentity">
        <span> qq号</span>
         <input type="text"  v-model="objs.qq_num"/>
    </div>
     <div class="indentity">
        <span>性别</span>
          <input type="radio" v-model="objs.sex" class="sex1" value="1"><p class="sex2">男</p>
         <input type="radio" v-model="objs.sex" class="sex3" value="0"><p class=" sex4">女</p> 
    </div>
     <div class="indentity">
        <span>最高学历</span>
         <input type="text"  v-model="objs.education"/>
    </div>
     <div class="indentity">
        <span>职称</span>
         <input type="text"  v-model="objs.job_rank"/>
    </div>
      <div class="indentity">
        <span>薪资水平</span>
         <input type="text"  v-model="objs.salary"/>
    </div>
      <div class="indentity">
        <span>入党时间</span>
          <em>{{objs.join_party_time}}</em>
        <!-- <input  type="date" v-show="true" name="joinPartyTime" id="joinPartyTime"
                   v-model="objs.join_party_time"/> -->

    </div>
      <div class="indentity">
        <span>党费最后缴纳时间</span>
          <em>{{objs.last_pay_time}}</em>
       <!-- <input  type="date" v-show="true" name="lastPayTime" id="lastPayTime"
                   v-model="objs.last_pay_time"/> -->
    </div>
    <div class="indentity">
        <span>当前身份</span>
       <select name="partyStatus" v-model="objs.party_status">
            <option value="2">党员</option>
            <option value="1">预备党员</option>
            <option value="0">积极分子</option>
          </select>
    </div>


    </div>
</template>

<script>
 import {  updateInfo} from "../../api/api"
export default {
   
 name:'information',
     data(){
      return{
          file:'',
          imageUrl: '',
         Pagetitle:'修改个人信息',
         objs:{'sex':1} //信息集合
      }
    },
     mounted() {
        if(this.$route.params.objs!=undefined){
        this.objs = this.$route.params.objs;
         this.imageUrl='http://118.178.85.48.:4000'+this.objs.header;
        }
     },
    methods:{
      change(file){
        this.imageUrl = URL.createObjectURL(file.raw);
        this.file=file.raw;
      },
       async  save(){
           let formData = new window.FormData();
           formData.append('username',this.objs.username);
           formData.append('id_card',this.objs.id_card);
           formData.append( 'phone',this.objs.phone);
         formData.append(  'nation',this.objs.nation);
         formData.append( 'wx_num',this.objs.wx_num);
         formData.append( 'qq_num',this.objs.qq_num);
         formData.append( 'age',this.objs.age);
         formData.append( 'sex',this.objs.sex);
         formData.append( 'education',this.objs.education);
         formData.append( 'job_rank',this.objs.job_rank);
         formData.append( 'address',this.objs.address);
         formData.append( 'salary',this.objs.salary);
         formData.append( 'party_status',this.objs.party_status);
         formData.append( 'hometown',this.objs.hometown);
         formData.append( 'userId',this.objs.id);
         if(Object.prototype.toString.call(this.file)=='[object File]'){
          formData.append( 'header',this.file);
         }
    // console.log(Object.prototype.toString.call(this.file));
          let result= await updateInfo(formData);
          if(result.status==0){
              this.$message({
                message:result.massage,
                type:"success"  
              })
          }
            
        },

        prev(){
                this.$router.go(-1)
            },
    // 上传成功回调
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
      this.file=file.raw;
    },
    // 上传前格式和图片大小限制
    beforeAvatarUpload(file) {
      const type = file.type === 'image/jpeg' || 'image/jpg' || 'image/webp' || 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!type) {
        this.$message.error('图片格式不正确!(只能包含jpg，png，webp，JPEG)')
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!')
      }
      return type && isLt2M
    }
  }
}
   window.onload = function () {
            let fs = document.documentElement.clientWidth * 100 / 720;
            document.querySelector("html").style.fontSize = fs + "px";
        }
</script>

<style lang="less" scoped>
 @import "../../assets/less/04-个人信息.less";
  @import "../../assets/less/zcm.less";

    .indentity{
        input{
        color: #999999;
       .fs(25); 
        text-align: center;
       }
    }
    em{
      color: #999999;
       .fs(25); 
       .mr(30);
    }
    .sex1{
        position: relative;
        left: 3.5rem;
    }
    .sex2{
        position: relative;
        left: 2.2rem;
    }
      .sex3{
        position: relative;
        left: 1rem;
    }
    .sex4{
        position: relative;
        right: 0.2rem;
    }
  header{
    background-color: #c50206;
    .lh(100);
    .fs(33);
    color: #fff;
    .flex_a();
 .icon-arrow-right{
     .fs(50);
     .ml(-80);
 }
    .icon-save{
        .fs(40);
    }
}

 .avatar-uploader{
  .w(150);
  .h(100);
  .mr(30);
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  background: url('../../assets/Img/1.jpg') no-repeat;
  background-size: 100% 100%;
  img{
  .w(150);
  .h(100);
  }
}
.avatar-uploader-icon{
  font-size: 0;
  color: #fff;
  width: 120px;
  height: 120px;
  line-height: 120px;
  text-align: center;
}
.avatar-uploader-icon:hover{
  font-size: 28px;
  background-color: rgba(0, 0, 0, .3);
}
</style>