var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"content"},[_c('h1',{staticClass:"red"},[_vm._v("党史上的今天")]),_c('h2',[_vm._v(" 1949年10月2日 苏联政府决定建立同中华人民共和国的外交关系，并派遣大使 ")]),_c('p',[_vm._v(" 1949年10月2日 苏联副外长葛罗米柯致电中国外长周恩来表示:苏联政府决定建立同中华人民共和国的外交关系，并派遣大使。同日，苏联宣布断绝与国民党政府的外交关系。3日，周恩来复电表示热忱欢迎，并互派大使。自1949年10月至1950年1月，新中国先后与保加利亚、罗马尼亚、匈牙利、朝鲜民主主义人民共和国、捷克斯洛伐克、波兰、蒙古、德意志民主共和国、阿尔巴尼亚和越南民主共和国建立外交关系。至1951年底，新中国与印度、缅甸、巴基斯坦、印度尼西亚、瑞典、丹麦、瑞士、列支敦士登、芬兰建立外交关系。 ")]),_c('h2',[_vm._v(" 1952年10月2日 刘少奇率团参加苏共十九大，毛泽东委托刘少奇就中国向社会主义过渡的设想，向斯大林征求意见 ")]),_c('p',[_vm._v(" 1952年10月2日 刘少奇率团参加苏共十九大，毛泽东委托刘少奇就中国向社会主义过渡的设想，向斯大林征求意见。20日，刘少奇给斯大林写了一封长信，对中国过渡到社会主义所采取的步骤作了说明。其要点是：在中国现实的国民经济中，社会主义的国营经济已经占居明显的优势，这是中国有可能开始向社会主义过渡的物质基础。预计经过两个五年计划建设，在公私经济比重中私人工业将不到10%，而且大体都要依赖国家供给原料、收购成品。到那时，可以将这一部分私人工业收归国家经营。至于国有化的时机及具体方式，还要看将来的情形来决定。在农业中，准备在今后10年至15年内将中国多数农民组织在农业生产合作社和集体农场内。估计改造手工业和组织手工业生产合作社，时间可能需要更长。这封信反映了中共中央领导人对中国怎样过渡到社会主义的新思路。 ")]),_c('h2',[_vm._v("1964年10月2日 大型音乐舞蹈史诗《东方红》在人民大会堂隆重首演")]),_c('p',[_vm._v(" 1964年10月2日 为庆祝中华人民共和国成立15周年，大型音乐舞蹈史诗《东方红》在人民大会堂隆重首演，3500多名专业和业余文艺工作者参加演出。《东方红》是在周恩来的倡议和指导下，集中了全国音乐、舞蹈、诗歌、美术等各方面著名艺术家的智慧集体创作完成的。《东方红》是一首高昂激越的中国革命颂歌。它以史诗般的宏伟风格和波澜壮阔的音乐舞蹈场面，生动形象地概括和展现了从中国共产党诞生到新中国成立，并开始进行社会主义建设的伟大历程，开创了歌唱、舞蹈和戏剧相结合的综合性大型歌舞表演的新形式、新道路。1965年10月，《东方红》由北京电影制片厂、八一电影制片厂和中央新闻纪录电影制片厂联合摄制成彩色宽银幕影片，在全国各地放映，产生了深远的社会影响。 ")])])}]

