<template>
    <div>
      <!-- 头部 -->
  <HeaderBar :Headname='Pagetitle'></HeaderBar>
       <!-- 主体内容 -->
        <div class="choice">请选择支付方式</div>
      <div class="list">
        <div v-on:click="showImg('wechat')" class="item item-icon-left payItem item-icon-left" href="#">
          <i class="iconfont icon-icon-test "></i>
         <span>微信</span> 
          <i class="iconfont icon-zuojiantou"></i>
        </div>
        <div v-on:click="showImg('alipay')"  class="item  item-icon-left item-icon-right" style="border-top: none" href="#">
          <i class="iconfont icon-zhifubao"></i>
         <span>支付宝</span>  
          <i class="iconfont icon-zuojiantou"></i>
        </div>
      </div>
          <div v-on:click="close" v-show="showPayWrap" class="payWrap">
      <div class="payImgWrap">
        <div class="payText">{{payText}}</div>
        <img v-show="showAlipay" src="../../assets/Img/alipay.png" alt="">
        <img v-show="showWechat" src="../../assets/Img/wechat.png" alt="">
      </div>
    </div>
    </div>


  
</template>

<script>
import HeaderBar from "../../components/HeaderBar";
export default{
      data(){
        return {
          isplay:false,
          payText:"",
          showWechat:false,
            showAlipay:false,
            Pagetitle:'缴纳党费',
          showPayWrap:false
        }
      },
     components:{
      HeaderBar
    },
      methods:{
        close(){
          this.showWechat=false;
          this.showAlipay=false;
          this.showPayWrap=false;
        },
        showImg(str){
          this.showPayWrap=true;

          if(str=="wechat"){
            this.showWechat=true;
            this.showAlipay=false;
            this.payText="微信支付"
          }else {
            this.payText="支付宝支付"
            this.showWechat=false;
            this.showAlipay=true;
          }

        }
      }
    }
     window.onload = function () {
            let fs = document.documentElement.clientWidth * 100 / 720;
            document.querySelector("html").style.fontSize = fs + "px";
        }
</script>
<style lang="less" scoped>
  @import "../../assets/less/zcm.less";
  .choice{
      .mt(20);
      .fs(30);
      .mb(20);       
  }
  .item{
    padding-top: 20px;
    padding-bottom: 20px;
    border-top: 1px solid #999;
    border-bottom: 1px solid #999;
    span{
        .fs(30);
        .ml(40);
        position: relative;
        top:-0.15rem;
    }
  }
  
  .payText{
    font-size: 20px;
    line-height:2;
    width: 100%;
    text-align: center;
  }
  .payWrap{
    position: fixed;
    top: 0;
    left:0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
    z-index: 10;
  }
  .payImgWrap{
    width: 300px;
    height: 300px;
    /*padding: 20px;*/
    background: white;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
  }
  .payImgWrap img{
    width: 100%;
  }
  .icon-icon-test{
      color: #1afa29;
      .fs(70);
      .ml(20);
  }
  .icon-zhifubao{
      color:#1296db;
    .fs(70);
    .ml(20);
  }
  .icon-zuojiantou{
      .fs(50);
      position: fixed;
      right:0px;
      .mr(20);
      .mt(10);
      color:#999;
  }
 
</style>
