<template>
  <div class="con">
    <HeaderBar :Headname="Pagetitle"></HeaderBar>
    <div class="item">
      <div class="it-hea">
        <div class="left">
          <i class="iconfont icon-touxiang txi"></i>
        </div>
        <div class="right">
          <div class="top">
            <div>{{ TZinfo.username }}</div>
            <div class="lxyz">党员互动</div>
          </div>
          <div class="bottom">
            <span class="time">
              <i class="iconfont icon-shijian"></i
              >{{ TZinfo.create_time | format }}</span
            >
            <span><i class="iconfont icon-gongkai"></i>公开</span>
          </div>
        </div>
      </div>
      <div class="it-cont">
        <span>{{ TZinfo.content }}</span>
      </div>
    </div>
    <div class="item" v-for="(item,index) in datalist" :key="index">
      <div class="it-hea">
        <div class="left">
          <i class="iconfont icon-touxiang txi"></i>
        </div>
        <div class="right">
          <div class="top">
            <div>{{ TZinfo.username }}</div>
            <div class="lxyz">党员互动</div>
          </div>
          <div class="bottom">
            <span class="time">
              <i class="iconfont icon-shijian"></i
              >{{ TZinfo.create_time | format }}</span
            >
            <span><i class="iconfont icon-gongkai"></i>公开</span>
          </div>
        </div>
      </div>
      <div class="it-cont">
        <span>{{ item }}</span>
      </div>
    </div>
    
    <div class="last">
        <input type="text" placeholder="评论内容" v-model="content"/>
        <div class="button">
            <em @click="replyTZ">评论</em>
        </div>
    </div>
  </div>
</template>

<script>
import { getTZinfo, getTZlist, replyTZ } from "../../../api/api";
import { formateDate } from "../../../api/util";
import HeaderBar from "../../../components/HeaderBar";

export default {
  name: "",
  filters: {
    format(data) {
      return formateDate(data);
    },
  },
  components: {
    HeaderBar,
  },
  data() {
    return {
      Pagetitle: "帖子回复",
      forumId: "",
      userId: "",
      TZinfo: {},
      content:'',
      datalist:[],
    };
  },
  created() {
    this.forumId = this.$route.query.forumId;
    this.userId = this.$route.query.userId;
    // console.log(this.forumId);
    // console.log(this.userId);
    this.getTZinfo();
  },
  methods: {
    //帖子回复
    async replyTZ(){
        // console.log(this.content);
        if(this.content == ''){
            this.$message({
                type: 'warning',
                message: '评论不能为空哦'
            });
            return;
        }
        let res = await replyTZ({forumId:this.forumId,userId:this.userId,comment:this.content})
        // console.log(res);
        if(res.status == 0){
            this.$message({
                type: 'success',
                message: '评论成功!'
            });
            this.datalist.push(this.content)
            this.content = '';
        }
    },
    //根据ID获取帖子信息
    getTZinfo() {
      getTZinfo(this.forumId).then((res) => {
        this.TZinfo = res.data;
      });
    },
  },
};
</script>
<style lang='less' scoped>
@import url(../../../assets/less/zhao/xzTaoLun.less);
@import url(../../../assets/less/zhao/assets/icon/iconfont.css);
.con{
    box-sizing:border-box;
}
.item{
    padding: 0.1rem;
    border-radius: 0.5rem;
    background-color: #eee;
    box-sizing:border-box;
}
.last{
    height: 1rem;
    width: 100%;
    background-color: #eee;
    border-radius: 0.5rem;
    position: fixed;
    bottom: 0;
    display: flex;
    input{
        width: 80%;
        margin: 0.2rem 0.6rem;
        padding: 0 0.1rem;
        border: 0.01rem solid red;
        border-radius: 0.1rem;
        color: black;
    }
    div{
        width: 20%;
        em{
            display: inline-block;
            width: 1rem;
            height: 0.6rem;
            margin: 0.2rem 0;
            line-height: 0.6rem;
            text-align: center;
            background-color: red;
            color: #fff;
            border-radius: 0.1rem;
        }
    }
}

</style>