<template>
    <div class='score'>
      <!-- 头部 -->
  <HeaderBar :Headname='Pagetitle'></HeaderBar>
       <!-- 主体部分 -->
    <section>
        <div class="content">
            <div class="score">
                <div class="sco">
                    <span>{{total_score}}</span>
                    <img src="../../assets/Img/积分bg.png" alt="">
                </div>
            </div>
            <div class="info">
                <div class="selfinfo" @click="todetail">
                    <img src="../../assets/Img/积分明细icon.png" alt="">
                    <span >积分明细</span>
                     <i class="iconfont icon-zuojiantou"></i>
                </div>
                 <img class="line" src="../../assets/Img/line.png" alt="">
                <div class="argular">
                    <span>积分规则</span>
                    <img src="../../assets/Img/问号icon.png" alt="">
                </div>
                <div v-for="data in objs" :key="data.id">
                    <div class="move">
                    <span>{{data.type_name}}</span>
                    <em>单次{{data.single_desc}}                 max      {{data.max_num}}</em>
                </div>
               </div>
            </div>
        </div>
    </section>
    </div>
</template>

<script>
import HeaderBar from "../../components/HeaderBar";
import {getAllRule}  from "../../api/api"

export default {
 name:'information',
     data(){
      return{
      Pagetitle:'个人积分',
      objs:[],
      total_score:''
      
      }
    },
     components:{
      HeaderBar
    },
    created(){
        this.getAllRule();
    },
    methods:{
         async getAllRule(){
            let result = await getAllRule();
            this.objs=result.data;
            this.total_score=localStorage.getItem("total_score")
        },

        todetail(){
            this.$router.push('/scoredetail')
        }
    }
}
   window.onload = function () {
            let fs = document.documentElement.clientWidth * 100 / 720;
            document.querySelector("html").style.fontSize = fs + "px";
        }
</script>

<style lang="less" scoped >
 @import "../../assets/less/03-个人量化积分.less";
@import "../../assets/css/normalize.css";
  @import "../../assets/less/zcm.less";

.icon-zuojiantou{
    .mr(25);
    .fs(30);
}

</style>