<template>
  <div class="">
    <HeaderBar :Headname="Pagetitle"></HeaderBar>
    <div class="content"
            v-infinite-scroll="loadMore"
            infinite-scroll-disabled="loading"
            infinite-scroll-distance="0"
            infinite-scroll-immediate-check="false">
      <router-link
        to="" 
        class="item" 
        tag="div"
        v-for="item in dataList"
        :key="item.forum_id"
        >
          <div class="it-hea">
            <div class="left">
              <i class="iconfont icon-touxiang txi"></i>
            </div>
            <div class="right">
              <div class="top">
                <div>{{item.username}}</div>
                <div class="lxyz">党员互动</div>
              </div>
              <div class="bottom">
                <span class="time">
                  <i class="iconfont icon-shijian"></i>{{item.create_time | format}}</span
                >
                <span><i class="iconfont icon-gongkai"></i>公开</span>
              </div>
            </div>
          </div>
          <div class="it-cont"><span>{{item.content}}</span></div>
          <router-link tag="div" :to="{path:'/interactionDetail',query: {forumId:item.forum_id,userId:item.user_id}}" class="it-hf">
            <i class="iconfont icon-huifu"></i>回复
          </router-link>
      </router-link>
      <p class="more">{{msg}}</p>

    </div>

    <div class="tjia" @click="showAdd"><i class="iconfont icon-iconaddsuccess"></i></div>
      <!-- 弹出层增加帖子 -->
      <div class="publishWrap" v-show="isshow">
        <div  class="publishForm">
          <textarea v-model="content"></textarea>
          <div>
            <em class="left" @click="addTZ">发布</em>
            <em class="right" @click="cansel">取消</em>
          </div>
        </div>
      </div>
    

  </div>
</template>

<script>
import { addTZ, getTZlist } from '../../../api/api';
import { formateDate } from '../../../api/util';
import HeaderBar from "../../../components/HeaderBar";

export default {
  name: "",
  filters:{
    format(data){
        return formateDate(data);
    }
  },
  components: {
    HeaderBar,
  },
  data() {
    return {
      content:'',
      isshow:false,
      Pagetitle: "党员互动",
      page:1,
      limit:9,
      dataList:[],
      total:'',
      msg:'加载更多中...',
      loading:false,
    };
  },
  created(){
    this.getTieZiList();
  },
  methods:{
    //发布帖子
    async addTZ(){
      // console.log(this.content);
      if(this.content == ''){
        this.$message({
            type: 'warning',
            message: '帖子不能为空!'
        });
        return;
      }
      let res = await addTZ({userId:"2",username:'冰洁',content:this.content,type:11})
      if(res.status == 0){
        this.$message({
            type: 'success',
            message: '添加帖子成功!'
        });
        this.content = '';
        this.isshow = false
        this.getTieZiList();
      }
    },
    //帖子弹出层缩回
    cansel(){
      // console.log("...");
      this.isshow = false
    },
    //帖子弹出层弹出
    showAdd(){
      console.log("...");
      this.isshow = true
    },
    //获取帖子列表
    async getTieZiList(){
      let res = await getTZlist({page:this.page,limit:this.limit})
      // console.log(res);
      this.dataList = [ ...this.dataList, ...res.data]
      this.loading = false;
      this.total = res.total
      if(this.dataList.length == this.total){
          this.msg = '------没有数据啦------'
          return;
      }
    },
    //加载更多
    loadMore() {
      this.loading = true //不触发loadMore函数
      this.page++;
        // console.log(this.page);
      if(this.dataList.length == this.total){
          this.msg = '------没有数据啦------'
          return;
      }
      this.getTieZiList();
    },
  }
};
</script>
<style lang='less' scoped>
@import url(../../../assets/less/zhao/xzTaoLun.less);
@import url(../../../assets/less/zhao/assets/icon/iconfont.css);

.publishWrap{
    position: fixed;
    bottom: 0;
    height: 100%;
    width: 100%;
    z-index: 999;
    background: rgba(0,0,0,.5);
    .publishForm{
          width: 100%;
          height: 3rem;
          position: fixed;
          bottom: 0;
          padding: 10px;
          background: #f1f1f1;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          textarea{
            height: 70%;
            border: 1px solid #666;
            border-radius: 0.1rem;
          }
          div{
            height: 25%;
            display: flex;
            justify-content: space-between;
            em{
              width: 1.3rem;
              height: 0.6rem;
              line-height: 0.6rem;
              text-align: center;
              font-size: 0.3rem;
              border-radius: 0.1rem;
            }
            .left{
              background-color: red;
              color: #fff;
            }
            .right{
              background-color: #555;
              color: #fff;
            }
          }

    }
}

</style>