<template>
  <div class="">
        <HeaderBar :Headname='Pagetitle'></HeaderBar>
         <section 
            v-infinite-scroll="loadMore"
            infinite-scroll-disabled="loading"
            infinite-scroll-distance="0"
            infinite-scroll-immediate-check="false"
            >
        <router-link
            :to="{path:'/newsDetail',query: {NewsId: item.news_id}}" 
            tag="div" 
            class="content" 
            v-for="item in NewsList" 
            :key="item.news_id"
            >
                <div class="left">
                    <img :src="'http://118.178.85.48:4000'+item.pic" alt="">
                </div>
                <div class="right">
                    <span>{{item.title}}</span>
                    <div class="eye">
                        <div class="rig">
                            <em>{{item.create_time | format}}</em>
                            <img src="../../../assets/Img/12-eye.png" alt="">
                            <em>817769</em>
                        </div>
                    </div>
                </div>
        </router-link>
        <p class="more">{{msg}}</p>
    </section>
  </div>
</template>
<script>
import { Loading } from 'element-ui';
import { getNewsDetailById, getNewsList } from '../../../api/api';
import { formateDate } from '../../../api/util';

import HeaderBar from "../../../components/HeaderBar";

export default {
  name: "",
  filters:{
      format(data){
          return formateDate(data);
      }
  },
  components: {
      HeaderBar
  },
  data() {
    return {
        Pagetitle:'政治学习',
        NewsList:[],
        page:1,
        limit:9,
        total:'',
        msg:'加载更多中...',
        loading:false,
    };
  },
  created(){
      this.getNewsList();
  },
  methods:{
    // 获取新闻列表
    async  getNewsList(){
        let res = await getNewsList({page:this.page,limit:this.limit,type:8});
        // console.log(res);
        this.NewsList = [ ...this.NewsList, ...res.data]
        this.loading = false;
        this.total = res.total
        if(this.NewsList.length == this.total){
          this.msg = '------没有数据啦------'
          return;
        }
    },
    //加载更多
    loadMore() {
      this.loading = true //不触发loadMore函数
      this.page++;
        // console.log(this.page);
      if(this.NewsList.length == this.total){
          this.msg = '------没有数据啦------'
          return;
      }
      this.getNewsList();
    },
  }
};
</script>
<style lang='less' scoped>
@import url(../../../assets/less/du/05-新闻工作眼.less);

</style>