<template>
  <div class="">
      <HeaderBar :Headname='Pagetitle'></HeaderBar>

      <div class="con" v-show="isshow">         
        <el-upload
            class="upload-demo"
            action=""
            :auto-upload='false'
            :http-request="handleChange"
            ref="upload"
            :on-preview="handlePictureCardPreview"
            list-type="picture"
            :multiple='true'
            >
            <div class="last"></div>
        </el-upload>  
        </div>
        <div class="footer" v-show="isshow">
            <div class="one">
                <a href="#"  @click.prevent="addReport">提交审核</a>
            </div>
        </div>

        <div class="check" v-show="checkshow">
            <div class="logo">
                <img src="../../../assets/Img/reviewing.png" alt="">
            </div>
            <div class="content">
                正在审核中
            </div>
            <div class="foot" @click="back">
                关闭
            </div>
        </div>

  </div>
</template>

<script>
import { insertSXBG, lookReport } from '../../../api/api';
import HeaderBar from "../../../components/HeaderBar";
export default {
  name: "",
  components: {
      HeaderBar
  },
  data() {
    return {
        isshow:true,
        checkshow:false,
        Pagetitle:'心的总结',
        pic:'',
        dialogImageUrl: '',
        dialogVisible: false,
        files:[],
        FilesList:[],
    };
  },
  created(){
      this.Lookreport();
  },
  methods:{
      //返回上一级
      back(){
          this.$router.go(-1)
      },
      //查看心得总结状态
      Lookreport(){
          lookReport({userID:'2',istype:'2'}).then(res=>{
              console.log(res);
            if(res.data.type == 0){
                this.checkshow = true;
                this.isshow = false
            }
          })
      },
      //提交审核
      addReport(){
          this.$refs.upload.submit();
      },
      //图片预览
      handlePictureCardPreview(file) {
        this.dialogVisible = true;
      },
      handleChange(e) {
        const form = new FormData();
        form.append("userID","2");
        form.append("istype",2);
        form.append("files",e.file);
        insertSXBG(form).then(res=>{
            if(res.status == 0){
                this.$message({
                    type: 'success',
                    message: '提交成功!'
                });
                this.checkshow = true;
                this.isshow = false
            }
        })
      }
    },
    
};
</script>
<style lang='less' scoped>
@import url(../../../assets/less/zcm.less);
.check{
    z-index: 999;
    .logo{
        height: 2rem;
        width: 2rem;
        margin: 1rem auto 0.5rem;
        img{
            background-size: 100%;
            height: 2rem;
            width: 2rem;
        }
    }
    .content{
        font-size: 0.4rem;
        text-align: center;
        height: 0.6rem;
        line-height: 0.6rem;
    }
    .foot{
        font-size: 0.4rem;
        text-align: center;
        height: 0.6rem;
        line-height: 0.6rem;
        margin: 0 1.5rem;
        border-radius: 10px;
        margin-top: 1rem;
        background-color: red;
        color: #fff;
    }
}

.el-upload--picture-card{
  width: 1rem;
  height: 1rem;
}
.con{
    padding: 0.7rem 0.3rem;
    display: flex;
    justify-content: space-around;
    div{
        width: 1.2rem;
        height: 1.2rem;
    }
    .last{
        background: url(../../../assets/Img/图片.png) no-repeat;
        background-size: 100%;
    }
}
.footer{
    position: absolute;
    bottom: 0.4rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    .one em{
        display: inline-block;
        border: 0.1rem solid #fff;
        width: 0rem;
        height: 0rem;
        border-bottom: 0.1rem solid transparent;
        border-right: 0.1rem solid transparent;
        border-left: 0.1rem solid transparent;
        position: absolute;
        right: 0.3rem;
        top: 0.3rem;
    }
    div{
        width: 75%;
        display: flex;
        justify-content: center;
        a{
            position: relative;
            .fs(28);
            width: 100%;
            .h(70);
            line-height: 0.7rem;
            text-align: center;
            border-radius: 0.1rem;
            color: #fff;
            background-color: red;
            margin-bottom: 0.2rem;
        }
    }

}
</style>