<template>
 <div id="page-login" class=" page page-current">
    <!--头部开始-->
  <HeaderBar :Headname='Pagetitle'></HeaderBar>
    <!--头部结束-->
    <!--中间内容-->
    <div class="scroll-content">
      <div class="logo">
         <img src="../../assets/Img/logo.png" alt="">
      </div>
      </div>
      <form ref="form" :model="loginForm" class="login-form">
        <input type="text" v-model="loginForm.id_card" id="idcard" placeholder="身份证号" value="" />
        <input type="password" v-model="loginForm.password" placeholder="密码" value="" />
        <input type="button"  @click="login" id="login" value="登录" />
      </form>

    </div>
  

</template>

<script>

import { showLoading, hideLoading } from '../../components/loading';
import HeaderBar from "../../components/HeaderBar";
  import axios from 'axios'
  import { login} from "../../api/api"
  export default{
    data(){
      return{
        isplay:false,
        loginForm:{
            id_card: '',
            password: ''
        },
        
        Pagetitle:'登录',
     
      }
    },
     components:{
     HeaderBar
    },
    methods: {
      login(){
        if(this.id_card==''||this.password==''){
            this.$message({
            message:'账号密码不能为空',
            type:'error'
        })
        }
        else{
        showLoading();
          login(this.loginForm).then(data => {
            console.log(data);
          if(data.status==0){
            localStorage.setItem("userId",data.data.id) 
            localStorage.setItem("id",this.loginForm.id_card)
            localStorage.setItem("username",data.data.username)
            localStorage.setItem("tx",'http://118.178.85.48.:4000'+data.data.header)
           localStorage.setItem("token",'Bearer'+' '+data.token)
            this.$message({
                 message:'登录成功',
                        type:'success'
             })
              hideLoading();
              this.$router.push('/index')
          }else{
             hideLoading();
          }
          }).catch(()=>{
           hideLoading();
          })

        
      
            } 
      }
    }
  }
    var fs = document.documentElement.clientWidth / 720  * 100; 
    document.querySelector("html").style.fontSize = fs + "px";
</script>

<style lang="less" scoped>

@import "../../assets/less/login.less";
@import '../../assets/css/iconfont.css';

</style>
