import { options } from 'less'
//放ajax请求函数  目的：在组件中发送ajax请求  类似于调用函数
import http from './http'

/*************************************首页相关接口*****************************************************/
export function getLunbo(options){
    return http.post('carousel/pageQuery',options)
}

/************************************新闻管理模块******************************************************/
//获取新闻列表
export function getNewsList(options){
    return http.post('news/getAllNews',options)
}
//根据id获取新闻详情
export function getNewsDetailById(id){
    return http.post('news/getNewsById',{newID:id})
}

/****************************************帖子管理*******************************************************/
//获取帖子列表
export function getTZlist(options){
    return http.post('forum/pageQueryList',options)
}
//根据帖子id获取帖子信息
export function getTZinfo(id){
    return http.post('forum/findForumById',{ForumId:id})
}
//帖子回复
export function replyTZ(options){
    return http.post('forum/insertHuiFu',options)
}
//插入贴子
export function addTZ(options){
    return http.post('forum/insertForum',options)
}

/****************************************掌上组织生活******************************************************/
//添加报告
export function insertSXBG(options){
    return http.post('report/insertReport',options)
}
//查看报告状态
export function lookReport(options){
    return http.post('report/selectAccept',options)
}

/************************用户************************************* */

/**  用户登录 */
export function login(options){
    return http.post('/admin/userLogin',options)
}

/** 更改密码 */
export function updatePwd(options){
    return http.post('user/updatePWD',options)
}

/** 获取用户信息 */
export function getUserInfo(options){
    return http.post('user/getUserInfo',options)
}

/** 修改用户信息 */
export function updateInfo(options){
    return http.post('user/updateUserInfo',options)
}
/***************************积分************************************ */

/** 查看所有积分规则 */
export function getAllRule(){
    return http.post('integral/getAllRule')
}

/** 查询用户的积分 */
export function getUserIntegral(options){
    return http.post('integral/pageQueryAllUser',options)
}
